import React, {useState} from 'react';
import {useDispatch} from "react-redux";
import "./Auth.css";
import NavBar from "../components/NavBar";


const Auth = () => {
    const [title, setTitle] = useState('')
    const [author, setAuthor] = useState('')
    const [year, setYear] = useState('')
    const [style, setStyle] = useState('')
    const [technique, setTechnique] = useState('')
    const [description, setDescription] = useState('')
    const [file, setFile] = useState(null)
    const dispatch = useDispatch()
    const selectFile = (e) => {
        setFile(e.target.files[0])
    }

    const addProduct = (e) => {
        e.preventDefault()
        const data = {
            id: Date.now(),
            title: title,
            author: author,
            year: year,
            style: style,
            technique: technique,
            description: description,
            img: file
        }

        dispatch({type: 'CREATE_PRODUCT', data})
    }

    return (
        <>
            <NavBar/>
            <section className="form-auth">
                <h1 className="heading"><span> Добавление </span> новой картины </h1>
                <div className="row">
                    <form action="">
                        <input type="text" value={title} className="box" placeholder="Название" onChange={e => setTitle(e.target.value)} />
                        <input type="text" value={author} className="box" placeholder="Автор" onChange={e => setAuthor(e.target.value)}/>
                        <input type="text" value={year} className="box" placeholder="Год создания" onChange={e => setYear(e.target.value)}/>
                        <input type="text" value={style} className="box" placeholder="Стиль" onChange={e => setStyle(e.target.value)}/>
                        <input type="text" value={technique} className="box" placeholder="Техника" onChange={e => setTechnique(e.target.value)}/>
                        <input type="text" value={description} className="box" placeholder="Описание" onChange={e => setDescription(e.target.value)}/>
                        <input type="file" className="box" onChange={selectFile} />
                        <button type="submit" className="btn" onClick={addProduct}>Добавить картину</button>
                    </form>
                    {/*<div className="image">*/}
                    {/*    <img src={img1} alt="auth"/>*/}
                    {/*</div>*/}
                </div>
            </section>
        </>
    )
};

export default Auth;