export const reducer = (state = initialState, action) => {
    switch (action.type) {
        case 'CREATE_PRODUCT':
            return {...state, cards: [...state.cards, action.data]}
        case 'REMOVE_PRODUCT':
            return {...state, cards: state.cards.filter(product => product.id !== action.id)}
        default:
            return state
    }
}
