import React from "react";
import {useParams} from "react-router-dom";
import {useSelector} from "react-redux";
import NavBar from "../components/NavBar";
import "./ProductPage.css";


const ProductPage = () => {
    const param = useParams()
    const id = param.id - 1
    const product = useSelector(state => state.cards[id])
  return (
      <>
          <NavBar/>,
          <section className="card">

              <h1 className="heading"><span> {product.subtitle} </span> {product.title} </h1>
              <div className="row">
                  <div className="img-container">
                      <img src={product.img} alt="Картина"/>
                  </div>

                  <div className="content">
                      <p>Автор:</p><span className="price">{product.author}</span>
                      <p>Год создания: {product.year}</p>
                      <p>Стиль: {product.style}</p>
                      <p>Техника: {product.technique}</p>
                      <p>{product.description}</p>
                  </div>
              </div>
          </section>
      </>
  );
};

export default ProductPage;